import React from "react";
import { Box, Heading, Text, Button, Container } from '@chakra-ui/react';

function Contact() {
  const openMap = () => {
    window.open("https://maps.app.goo.gl/eTNFqxGANcJnYVP5A", "_blank");
  };

  return (
    <Container maxW="container.lg" p={6} bg="gray.50" borderRadius="md" boxShadow="md">
      <Box>
        <Heading as="h1" size="2xl" textAlign="center" color="teal.600">Contact Us</Heading>
        <Text mt={4}>Contact information for Kathmandu Yoga.</Text>
        <Button 
          mt={4} 
          colorScheme="teal" 
          onClick={openMap}
        >
          Open Google Maps
        </Button>
      </Box>
    </Container>
  );
}

export default Contact;
