import React from "react";
import { Box } from "@mui/material";

function Home() {
  return (
    <div>
      <Box
        component="img"
        src="/static/images/everest-banner.jpg"
        alt="Mount Everest"
        sx={{ width: '100%', height: '400px', objectFit: 'cover', objectPosition: 'top' }}
      />
      <h1>Welcome to Kathmandu Yoga</h1>
      <p>Your journey to wellness starts here.</p>
    </div>
  );
}

export default Home;
